import { NgOptimizedImage } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import { FrontendLogger, getBizNestErrorResponseMessage } from '@biz-nest/core-client';
import { IDatePickerConfig } from 'ng2-date-picker';
import { ToastrService } from 'ngx-toastr';

import { adminVerify } from '@biz-nest/auth-client';
import {
  AppEnvironmentService,
  AppNameService, AuthService,
  DataService, LoaderSpinnerComponent, MainBarTitleService
} from '@front-shared-lib';
import { controlNames } from '@main-shared-lib';
import { environment } from '../../../../environments/environment';

interface Iverify {
  verificationToken: string;
}

@Component({
  selector: 'app-verify',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    NgOptimizedImage,
    RouterModule,
    LoaderSpinnerComponent
  ],
  templateUrl: './verify.component.html',
  styleUrls: ['./verify.component.scss']
})
export class VerifyComponent implements OnInit {
  toggleData = true;
  errMsg?: string;
  form: FormGroup<{
    [K in keyof Iverify]: FormControl
  }>;

  posting = false;
  date = new Date();
  config: IDatePickerConfig = {
    format: '"DD-MM-YYYY"'
  };

  rememberMeDays: number;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    public auth: AuthService,
    private dataService: DataService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.rememberMeDays = this.route.snapshot?.params['rememberMeDays'];
    this.form = this.fb.group({
      verificationToken: ['', Validators.compose([Validators.minLength(4), Validators.required])]
    });
  }

  get controlNames() {
    return controlNames(this.form.controls);
  }

  ngOnInit(): void {
    AppEnvironmentService.environment = {
      production: environment.production
    };
    FrontendLogger.error('after controller');
    this.mainBarTitle.title = 'Verify';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content:
        'Secure access to administrative features through the admin panel verification page.'
    });
  }

  async submit() {
    this.posting = true;
    try {
      const response = await adminVerify({
        verificationToken: this.form.get('verificationToken')?.value,
        rememberMeDays: this.rememberMeDays || 1
      });

      FrontendLogger.debug('Verify success', response);

      this.dataService.isSuperAdmin = true;
      this.auth.isLoggedIn = true;
      this.form.reset();
      this.router.navigate(['/']);
    } catch (error) {
      FrontendLogger.error('Verify error', error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Failed');
    } finally {
      this.posting = false;
    }
  }
}
